.main {
    display: flex;
    align-items: center;
    justify-content: center
}

.users {
    background:rgb(255, 255, 255);
    position: aboslute;
    width: 75%;
    height: 75%;
    border-radius: 25px;
    font-family: 'Montserrat', sans-serif;
    display: flex;
    align-items: center;
    flex-direction: column;
    animation: fadein 2s forwards;
    box-shadow: 5px 5px 30px 5px rgba(0, 0, 0, 0.2);
}

.users .header {
    font-weight: bolder;
    font-size: 24px;
    text-align: center;
    padding: 25px;

    
}
.users .list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.users .list img {
    width: 35px;
    border-radius: 25px;
    padding: 5px;
    filter: hue-rotate(calc(var(--order) * -10deg));
}
.users .list .user {
    font-size: 18px;
    background:  rgb(178, 96, 245); /* eventually use chroma.js to take average pfp color */
    filter: hue-rotate(calc(var(--order) * 10deg));
    background-size: 150%;
    padding: 8px 20px 8px 0px;
    border-radius: 5px;
    height:25px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:10px;
    color: white;
    text-decoration: none;
    transition: 0.3s;
    opacity: 0;
    animation: fadein 2s forwards;
    animation-delay: calc(var(--order) * 150ms);
}


@keyframes fadein {
    0% {
        opacity: 0
    }
    100% {
        opacity: 1
    }
}